import React from "react"
import BootstrapTable from 'react-bootstrap-table-next';
// import filterFactory, { textFilter, selectFilter } from 'react-bootstrap-table2-filter';
import cellEditFactory, { Type }  from 'react-bootstrap-table2-editor'
import paginationFactory from 'react-bootstrap-table2-paginator'

import { getUsers } from "../api"
import { connect } from "react-redux";

import { addChange, save } from "../redux/actions/users.action"



class Users extends React.Component{
	constructor(props){
		super(props)
		this.columns = [
			{
				text: 'ID',
				dataField: 'id',
				editable: false
			},
			{
				text: 'ФИО',
				dataField: 'name'
			},
			{
				text: 'Логин',
				dataField: 'login',
				editable: false
			},
			{
				text: 'Роль',
				dataField: 'role',
				editor: {
					type: Type.SELECT,
					options: [{
					  value: "0",
					  label: 'Администартор'
					}, {
					  value: "1",
					  label: 'Модератор'
					}, {
					  value: '2',
					  label: 'Редактор'
					}, {
					  value: '3',
					  label: 'Пользователь'
					}]
				 }
			},
			{
				text: 'Активен',
				dataField: 'active',
				editor: {
					type: Type.SELECT,
					options: [{
					  value: '0',
					  label: 'Не активен'
					}, {
					  value: '1',
					  label: 'Активен'
					}]
				 }
			}
		]

		this.state = {
			data: [],
			title: "Таблица для производства"
		}
	}
	

	componentDidMount(){
		getUsers((res) => {
			this.setState({ data: res.data })
		}, (err) => console.error(err))
	}

	render(){
		return(
			<div>
				<h1 style={{textAlign: "center", fontFamily: "RobotoBold", fontSize: "1.5em"}}>Управление пользователями</h1>
				<BootstrapTable striped hover condensed bordered={ true }
					id="table_users"
					keyField='id' 
					data={ this.state.data } 
					columns={ this.columns } 
					cellEdit={
						cellEditFactory({
							mode: 'click',
							afterSaveCell: (oldValue, newValue, row, column) => { this.props.addChange(row.id, column.dataField, newValue) }
						})
					}
					pagination={ paginationFactory() } // Footer for pages and view count item in pages
				/>
				<p className="footer-table">
						<button className="btn btn-primary">Новая строка</button>
						<button className="btn btn-primary" onClick={e => this.props.saveChanges()}>Сохранить</button>
					</p>
			</div>
		)
	}
}

const mapStateToProps = state => ({
	auth: state.app.authorization,
	user: state.user
})

const mapDispatchToProps = f => ({
	addChange: (id, col, value) => f(addChange(id, col, value)),
	saveChanges: () => f(save())
})

export default connect(mapStateToProps, mapDispatchToProps)(Users)